<template>
  <div>
    <ul v-if="examples">
      <li v-for="example in examples" :key="example.id">
        <span class="example-source" v-html="highlightLexicalItem(lexicalItem, example.source)">
        </span>
        <span class="example-source-language">
          ({{ example.source_language }})
        </span><br>
        {{ example.english }}
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'Examples',
  props: {
    lexicalItem: String,
    examples: Array,
  },
  methods: {
    highlightLexicalItem: function(lexicalItem, exampleSource) {
      let stem = lexicalItem.split("-")[0]
      return exampleSource.replace(stem, `<span class="lexical-item-highlight">${stem}</span>`)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.example-source {
  font-style: italic;
}
.example-source-language {
  color: #808080;
}
.lexical-item-highlight {
  font-weight: bold;
  color: #808080;
  font-style: normal;
}
</style>
