<template>
  <div class="hello">
    <v-select
      label="lexical_item"
      :options="options"
      @search="onSearch"
      :value="selectedWord"
      @input="onSelect"
      :filterable="false"
      placeholder="Look up a word…"
      class="search-input"
    >
      <template #no-options>
        Start typing to look up a word…
      </template>
    </v-select>
    <div id="search-results" v-if="selectedDictionaryEntry">
      <DictionaryEntry :entry="this.selectedDictionaryEntry" v-if="selectedDictionaryEntry"/>
    </div>
  </div>
</template>

<script>
import Vue from 'vue'
import vSelect from 'vue-select'
// import gyyDictionary from '@/assets/example_dictionary_db.json'
import DictionaryEntry from '@/components/DictionaryEntry.vue'
// import orderBy from 'lodash/fp/orderBy'
import debounce from 'lodash/fp/debounce'

import 'vue-select/dist/vue-select.css';


// Set the components prop default to return our fresh components
vSelect.props.components.default = () => ({
  OpenIndicator: {
    render: createElement => createElement('span', ' '),
  },
});
Vue.component('v-select', vSelect)

export default {
  name: 'SearchFunction',
  components: {
    DictionaryEntry,
    vSelect,
  },
  data() {
    return {
      options: [],
      selectedWord: null,
      selectedDictionaryEntry: null,
    }
  },
  // computed: {
  //   // options: function() {
  //   //   return orderBy(['lexical_item'], ['asc'], gyyDictionary);
  //   // },
  // }
  methods: {
    onSearch(search, loading) {
      if(search.length) {
        loading(true);
        this.search(loading, search, this);
      }
    },
    search: debounce(400, (loading, search, vm) => {
      fetch("https://zlpkw8v7t1.execute-api.ap-southeast-2.amazonaws.com/dev", {
        method: 'POST',
        headers: {
          'Accept': 'application/json',
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({
          "request" : "match",
          "input" : search
        })
      }).then(res => {
        res.json().then(json => (vm.options = json.output));
        loading(false);
      });
    }),
    onSelect(value) {
      fetch("https://zlpkw8v7t1.execute-api.ap-southeast-2.amazonaws.com/dev", {
        method: 'POST',
        headers: {
          'Accept': 'application/json',
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({
          "request" : "get",
          "input" : value
        })
      }).then(res => {
        res.json().then(json => (this.selectedDictionaryEntry = json.output))
      });
      this.options = []
    }
  },
}
</script>

