<template>
  <div>
    <h3>{{ entry.lexical_item }}</h3>
    <b-card
      tag="article"
      style="max-width: 40rem;"
      class="mb-2"
      v-for="(sense, index) in entry.senses"
      :key="sense.id"
    >
      <SenseNumber :number="index + 1"/>
      <SenseDetail :partOfSpeech="sense.part_of_speech" :languages="sense.languages"/>
      <Translations :translations="sense.translations"/>
      <Examples :examples="sense.examples" :lexicalItem="entry.lexical_item"/>
      <Etymology v-if="hasWordRelations(sense)" :etymology="sense.word_relations"/>
      <Usage v-if="hasUsageNotes(sense)" :usageNotes="sense.usage_notes"/>
    </b-card>
  </div>
</template>

<script>
import Etymology from '@/components/Etymology.vue'
import Examples from '@/components/Examples.vue'
import SenseDetail from '@/components/SenseDetail.vue'
import SenseNumber from '@/components/SenseNumber.vue'
import Translations from '@/components/Translations.vue'
import Usage from '@/components/Usage.vue'

export default {
  name: 'DictionaryEntry',
  components: {
    Etymology,
    Examples,
    SenseDetail,
    SenseNumber,
    Translations,
    Usage,
  },
  props: {
    entry: Object
  },
  methods: {
    hasWordRelations: function(sense) {
      return ("word_relations" in sense)
    },
    hasUsageNotes: function(sense) {
      return ("usage_notes" in sense)
    },
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
