<template>
  <div>
    {{ formattedTranslations }}
  </div>
</template>

<script>
import map from 'lodash/fp/map'

export default {
  name: 'Translations',
  props: {
    translations: Array,
  },
  computed: {
    formattedTranslations: function() {
      return map(o => o.description, this.translations).join("; ")
    },
  }
}
</script>

